import React from "react";
import { Link } from "react-router-dom";
import { bubble as Menu } from "react-burger-menu";
import "../App.css";

function Navb() {
  return (
    <div className="menu">
      <Menu isOpen={false}>
        <Link to="/">
          Home
        </Link>

        <Link to="/About">
          About
        </Link>

        <Link to="/Projects">
          Projects
        </Link>

        <Link to="/Contact">
         Contact
        </Link>
      </Menu>
    </div>
  );
}

export default Navb;
