import React from 'react';
import Typical from 'react-typical'
import Face from '../components/Face'
import '../App.css';


function Home() {
  return (
    <div className="Home">
      <Face />
      <h2>KAIQUE RIBEIRO</h2>
      <h1>HELLO</h1>
      <p>
        I'm a{' '}
        <Typical
          loop={Infinity}
          wrapper="b"
          steps={[
            'Software Engineering Student',
            1000,
            'Front-End Developer',
            1000,
            'Python Developer',
            1000,
            'Java Developer',
            1000,
            'C Developer',
            1000,
            'Pentester',
            1000,
            'Loving Son',
            1000,
          ]}
      />
    </p>
    </div>
  );
}

export default Home;
