import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom"
import ParticlesBg from "particles-bg";
import Header from "./components/Header";
import "./App.css";
import Home from "./views/Home";
import About from "./views/About"
import Projects from "./views/Projects"
import Contact from "./views/Contact"

function App() {
  return (
    <div className="App">
      <Router>
        
        <Header />

        <div className="p-3">
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/About">
            <About />
          </Route>
          <Route path="/Projects">
            <Projects />
          </Route>
          <Route path="/Contact">
            <Contact />
           </Route>
        </Switch>
        </div>

        <ParticlesBg color="#66FCF1" num={100} type="cobweb" bg={true} />
        <footer>Copyright @2020 Kaique Ribeiro de Campos</footer>
      </Router>
    </div>
  );
}

export default App;
