import React from "react";
import me from "../assets/images/face.png";
import resume from "../assets/Currículo Kaique Campos.pdf"
import "../App.css";

function About() {
  return (
    <div className="About">
      <img src={me} alt="My Face" />
      <h1>ABOUT ME</h1>
      <p>
      Hey my name is Kaique and I develop software.
      I'm a Software Engineering Student at UTFPR - Universidade Tecnológica Federal do Paraná - Cornélio Procópio,Paraná Brasil.
      I'm fascinated by how much can be accomplished in the technology field and I'm always looking for new learning experiences. 
      <a href={resume} target="_blank" rel="noopener noreferrer" download> Download My Resume</a>

      </p>
    </div>
  );
}

export default About;
